*{margin: 0; padding: 0; font-family: 'Uchen', serif;}
li{list-style: none;}
a{text-decoration: none; color: black;}




.header {
    width: 100%; height: 300px;
}
.gnb {
    width: 1276px; height: 30px;
    margin: 0 auto;
}
.gnb ul {
    float: right;
    position: relative;
}
.gnb ul li {
    display: inline-block;
    margin-right: 10px;
    line-height: 30px;
}
.gnb ul li:hover {
    text-shadow: 2px 2px 2px #666;
}
.gnb ul li a {
    font-family: 'Noto Serif KR', serif;
}

#chk { display: none;}
#chk:checked ~ .search_sheet {
    height: 40px;
}
#chk ~ .search_sheet {height: 0;}
.chk_btn {
    font-family: 'Noto Serif KR', serif;
    cursor: pointer;
}
.search_sheet {
    width: 300px; height: 0;
    text-align: center;
    position: absolute;
    top: 35px; right: -90px;
    transition: all .2s;
    overflow: hidden;
}
#search {
    width: 200px; height: 35px; 
    border: 0px;
    border-bottom: 1px solid black;
    font-size: 13px;
    font-family: 'Noto Serif KR', serif;
    outline: none;
}
.search_btn {
    width: 50px; height: 25px;
    font-family: 'Noto Serif KR', serif;
    background-color: white;
    border: 1px solid black;
    border-radius: 3px;
    cursor: pointer;
}
.search_btn:hover {
    background-color: black;
    color: white;
}
.header .logo {
    width: 957px; height: 135px;
    margin: 0 auto;
    margin-bottom: 70px;
    font-size: 150px;
    text-align: center;
    line-height: 135px;
}
.lnb {
    width: 600px;
    margin: 0 auto;
}
.lnb>ul>li>a{
    font-size: 30px;
}
.lnb>ul>li>a:hover {
    text-shadow: 3px 3px 3px #666;
}
.lnb li {
    width: 150px; height: 50px;
    margin: 0 auto;
    text-align: center;
    line-height: 50px;
    float: left;   
    background-color: white;
    position: relative;
}
.lnb li li {display: none;}
.lnb li:hover li {
    display: block;
}
.lnb li li:hover {
    font-weight: bold;
}


.wrap {
    width: 100%; height: auto;
    text-align: center;
}
.visual {
    width: 100%; height: 400px;
    min-width: 1276px;
    display: inline-block;
    background-position: center;
    animation-name: visual_banner;
    animation-duration: 12s;
    animation-iteration-count: infinite;
    margin-bottom: 30px;
}
@keyframes visual_banner {
    0% {background-image: url(./images/visual_01.jpg);}
    25% {background-image: url(./images/visual_02.jpg);}
    50% {background-image: url(./images/visual_03.jpg);}
    75% {background-image: url(./images/visual_04.jpg);}
    100% {background-image: url(./images/visual_01.jpg);}
}


.title {
    margin-top: 100px;
    margin-bottom: 50px;
}
.price_end {font-family: 'Noto Serif KR', serif;}
.new {
    width: 1276px; height: 400px;
    margin:  0 auto;
    overflow: hidden;
    margin-bottom: 40px;
}
.new div {
    float: left;
    margin-right: 20px;
}
.new div a {
    display: inline-block;
    width: 304px; height: 300px;
    overflow: hidden;
} 
.new div:last-child {margin-right: 0;}
.new div:hover img{
    scale: 1.2;
    overflow: hidden;
}


.issue {
    width: 1276px; height: 90px;
    margin-bottom: 50px;
    margin: 0 auto;
}
.issue a {
    position: relative;
    width: 309px; height: 90px;
    border: 1px solid black;
    display: inline-block;
    font-size: 25px;
    line-height: 90px;
    border-radius: 5px;
}
.issue a:after {
    position: absolute;
    content: "";
    width: 100%;
    height: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    background: black;
    transition: all 0.2s ease
}
.issue a:hover {
    color: white;
}
.issue a:hover:after {
    top: 0;
    height: 100%;
}

.weekly {
    width: 1276px; height: 400px;
    margin: 0 auto;
}
.weekly label {
    display: inline-block;
    margin-right: 15px;
    cursor: pointer;
    margin-bottom: 20px;
    font-size: 17px;
} 
.week04 {
    padding: 0 5px;
}
.weekly label:hover {
    text-shadow: 2px 2px 2px #ccc;
}
#weekly01, #weekly02, #weekly03, #weekly04 {
    display: none;
}
.weekly_sheet01, .weekly_sheet02, .weekly_sheet03, .weekly_sheet04 {
    width: 1276px; height: 300px;
    display: none;
}
#weekly01:checked ~ .weekly_sheet01 {display: block;}
#weekly01:checked ~ .week01 {
    font-weight: bold;
    border-bottom: 2px solid black;
}
#weekly02:checked ~ .weekly_sheet02 {display: block;}
#weekly02:checked ~ .week02 {
    font-weight: bold;
    border-bottom: 2px solid black;
}
#weekly03:checked ~ .weekly_sheet03 {display: block;}
#weekly03:checked ~ .week03 {
    font-weight: bold;
    border-bottom: 2px solid black;
}
#weekly04:checked ~ .weekly_sheet04 {display: block;}
#weekly04:checked ~ .week04 {
    font-weight: bold;
    border-bottom: 2px solid black;
}

.pro {
    width: 304px; height: 300px;
    float: left;
    margin-right: 20px;
    position: relative;
