#life {display: none;}
#life:checked ~ .life_sheet {height: 400px;}
#life:checked ~ .life_open{display: none;}
#life:checked ~ .life_close{display: block;}
#life ~ .life_open{display: block;}
#life ~ .life_close{display: none;}

.life_sheet {
    width: 1276px; height: 0;
    overflow: hidden;
    transition: all .2s;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 50px;
}

.life_open, .life_close {
    margin: 0 auto;
    width: 60px; height: 30px;
    font-family: 'Noto Serif KR', serif;
    border: 1px solid black;
    line-height: 30px;
    font-size: 18px;
    border-radius: 3px;
    cursor: pointer;
    transition: all .5s;
}
.life_open:hover {
    background-color: black;
    color: white;
    width: 90px;
}
.life_close:hover {
    background-color: black;
    color: white;
    width: 90px;
}

