}
.sns a {
    width: 150px; height: 150px;
    display: inline-block;
    border-radius: 50%;
    font-size: 30px;
    line-height: 150px;
    overflow: hidden;
    font-size: 30px;
    color: white;
    font-weight: bold;
    text-shadow: 4px 5px 4px black;
    transition: all .2s;
    margin-right: 40px;
    box-shadow: 4px 4px 4px #666;
}
.sns a:first-child {
    background-image: url(./images/twitter011.jpg);
    background-size: 100%;
}
.sns a:first-child:hover {
    color: black;
}
