.quick {
    width: 40px; height: 280px;
    position: fixed;
    top: 30%; right: 0;
    background-color: white;
}
.quick li {
    width: 40px; height: 40px;
    font-size: 20px;
    line-height: 50px;
    text-align: center;
    position: relative;
}
.quick li:nth-child(5) {
    background-image: url(./images/kakao.jpg);
    background-size: 100%;
    background-position-x: -1px;
}
.quick li p {
    display: none;
    width: auto; height: 30px;
    border: 1px solid black;
    border-radius: 3px;
    box-sizing: border-box;
    padding: 0 5px;
    line-height: 30px;
    position: absolute;
    font-family: 'Noto Serif KR', serif;
    font-size: 13px;
    background-color: white;
}
.quick li:first-child p {
    top: 2px; left: -45px;
}
.quick li:nth-child(2) p {
    top: 2px; left: -70px;
}
.quick li:nth-child(3) p {
    top: 2px; left: -58px;
}
.quick li:nth-child(4) p {
    top: 2px; left: -90px;
}
.quick li:nth-child(5) p {
    top: 2px; left: -70px;
}
.quick li:nth-child(6) p {
    top: 2px; left: -45px;
}
.quick li:nth-child(7) p {
    top: 2px; left: -63px;
}
.quick li:hover p{display: block;}
